























































import Vue from 'vue';

export default Vue.extend({
  name: 'App',

  components: {
  },

  data() {
    return {
      drawer: false,
      group: null,
    }
  },
  methods: {
    routerLink(name: string) {
      this.$router.push({
        name: name
      })
      this.drawer = false
    }
  },
  created() {
    (window as any).webkit.messageHandlers.FCM.postMessage(`START_RECIEVING`);
  }
